// src/components/Footer.js
import Profile from "../img/SlideFour/Slide4_profile.png"
import React, { useState, useEffect } from 'react';

const SlideFour = () => {
  const [screenSize, setScreenSize] = useState(window.innerWidth);

  const handleResize = () => {
    setScreenSize(window.innerWidth);
  };

  useEffect(() => {
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);
  return (
    <div className="slideFour">
          {screenSize < 750 ? (
            <div className="text phone">
              <p className='title phone'>Какие у меня цели?</p>  
              {screenSize <= 380 ? (
                <p className='information phone'>Одной из моих целей является достижение финансовой независимости, иметь возможность следовать своим желаниям без ограничений.  Вторая поставленая мною цель - чтобы жизнь была наполнена свободой выбора и яркими, незабываемыми моментами.
              </p>
              ) : (
                <p className='information phone'>Одной из моих целей является достижение финансовой независимости, иметь возможность следовать своим желаниям без ограничений.  Вторая поставленая мною цель - чтобы жизнь была наполнена свободой выбора и яркими, незабываемыми моментами. С выбраной мною професией достигнуть этого - вопрос времени!</p>
              )}
            
            </div> 
          ) : (
            <>
            <img src={Profile} className='profile'/>
            <div className="text">
              <p className='title'>Какие у меня цели?</p>
              <p className='information'>
              Одной из моих целей является достижение финансовой независимости, иметь возможность следовать своим желаниям без ограничений. Вторая поставленая мною цель - чтобы жизнь была наполнена свободой выбора и яркими, незабываемыми моментами. С выбраной мною професией достигнуть этого - вопрос времени!
              </p>
            </div>

            </>
          )}
    </div>
  );
};
export default SlideFour;
