// src/components/Footer.js
import Profile from "../img/SlideTwo/Slide2_profile.png"
import React, { useState, useEffect } from 'react';

const SlideTwo = () => {
  const [screenSize, setScreenSize] = useState(window.innerWidth);

  const handleResize = () => {
    setScreenSize(window.innerWidth);
  };

  useEffect(() => {
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);
  return (

    <div className="slideTwo">
          {screenSize <= 750 ? (

            <div className="text phone">
              <p className='title phone'>Сложна ли професия трейдера?</p>  
              {screenSize <= 380 ? (
                <p className='information phone'>Профессия трейдера требует точного контроля
                над временными рамками и рыночными
                трендами, что приводит к постоянному
                напряжению и неизбежной необходимости в
                непрерывном саморазвитии.
              </p>
              ) : (
                <p className='information phone'>Профессия трейдера требует точного контроля
                над временными рамками и рыночными
                трендами, что приводит к постоянному
                напряжению и неизбежной необходимости в
                непрерывном саморазвитии. Эта деятельность
                увлекает меня, так как рынок всегда
                динамичен, предоставляя новые возможности
                для инноваций и профессионального роста.
              </p>
              )}
              
            </div> 
          ) : (
            <>
            <img src={Profile} className='profile'/>
            <div className="text">
              <p className='title'>Сложна ли професия трейдера?</p>
              <p className='information'>Профессия трейдера требует точного контроля
                над временными рамками и рыночными
                трендами, что приводит к постоянному
                напряжению и неизбежной необходимости в
                непрерывном саморазвитии. Эта деятельность
                увлекает меня, и предоставляет возможности для профессионального роста.
              </p>
            </div>
            </>
          )}
    </div>
  );
};

export default SlideTwo;
