// src/components/Footer.js
import Profile from "../img/SlideThree/Slide3_profile.png"
import React, { useState, useEffect } from 'react';

const SlideThree = () => {
  const [screenSize, setScreenSize] = useState(window.innerWidth);

  const handleResize = () => {
    setScreenSize(window.innerWidth);
  };

  useEffect(() => {
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);
  return (
    <div className="slideThree">
          {screenSize < 750 ? (
            <div className="text phone">
              <p className='title phone'>В чем смысл жизни?</p>  
              <p className='information phone'>Смысл моей жизни - развитие и изучение нового.  Одним из моих хобби является чтение книг и общение с мудрыми людьми. Каждый человек для меня - это словно живая книга с уникальными приключениями и историями, которые хочется изучить и понять.
              </p>
              
            </div> 
          ) : (
            <>
            <div className="text">
              <p className='title'>В чем смысл жизни?</p>
              <p className='information'>
              Смысл моей жизни - развитие и изучение нового.  Одним из моих хобби является чтение книг и общение с мудрыми людьми. Каждый человек для меня - это словно живая книга с уникальными приключениями и историями, которые хочется изучить и понять.
              </p>
            </div>
            <img src={Profile} className='profile'/>
            </>
          )}
    </div>
  );
};
export default SlideThree;
