// src/components/Footer.js
import Profile from "../img/SlideFive/Slide5_profile.png"
import React, { useState, useEffect } from 'react';

const SlideFive = () => {
  const [screenSize, setScreenSize] = useState(window.innerWidth);

  const handleResize = () => {
    setScreenSize(window.innerWidth);
  };

  useEffect(() => {
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);
  return (
    <div className="slideFive">
          {screenSize < 750 ? (
            <div className="text phone">
              <p className='title phone'>Какие развлечения предпочитаю?</p>  

              {screenSize <= 380 ? (
                <p className='information phone'>Отдых для меня - это самые разнообразные виды деятельности, от стрельбы в тире до дружеских встреч в уютных заведениях. Также серьозно увлекаюсь верховой ездой. Я всегда открыт для новых впечатлений и убеждён, что движение - это жизни.
              </p>
              ) : (
                <p className='information phone'>Отдых для меня - это самые разнообразные виды деятельности, от стрельбы в тире до дружеских встреч в уютных заведениях. Также серьозно увлекаюсь верховой ездой. Я открыт для новых впечатлений и убеждён, что в жизни стоит использовать все возможности, чтобы накапливать опыт, который в конечном итоге 
              и формирует нашу личность.</p>
              )}
            
            </div> 
          ) : (
            <>
            <div className="text">
              <p className='title'>Какие развлечения предпочитаю?</p>
              <p className='information'>
              Отдых для меня - это самые разнообразные виды деятельности, от стрельбы в тире до дружеских встреч в уютных заведениях. Также серьозно увлекаюсь верховой ездой. Я открыт для новых впечатлений и  убеждён, что в жизни стоит использовать все возможности, чтобы накапливать опыт, который в конечном итоге 
              и формирует нашу личность.
              </p>
            </div>
            <img src={Profile} className='profile'/>
            </>
          )}
    </div>
  );
};
export default SlideFive;
